<template>
	<div
		id="computed"
		class="flex flex-column"
	>
		<Header>
			<h1 slot="title">计算器</h1>
		</Header>
		<div class="wrap flex-yc">
			<!-- <div class="tips">✅ Copy successfully !!! 🌈</div> -->
			<div :class="`tips ${tipShow ? 'show' : ''}`">✅ Copy successfully !!! 🌈</div>
			<el-form class="setting">
				<el-form-item label="屏幕宽度">
					<el-input v-model="viewport"> </el-input>
				</el-form-item>
			</el-form>
			<div
				class="flex-xb"
				style="width: 100%"
			>
				<ul class="jsq">
					<li
						class="flex-xc"
						title="/1920"
					>
						<p><span>PX</span> → <span>VW</span></p>
						<p @click="copyTextToClipboard(toVW)">
							<span>{{ toVW }}</span>
						</p>
					</li>
					<li
						class="flex-xc"
						title="/1920"
					>
						<p><span>PX</span> → <span>%</span></p>
						<p @click="copyTextToClipboard(toBaiFenBi)">
							<span>{{ toBaiFenBi }}</span>
						</p>
					</li>
					<li
						class="flex-xc"
						title="*1920"
					>
						<p><span>VW</span> → <span>PX</span></p>
						<p @click="copyTextToClipboard(toPX)">
							<span>{{ toPX }}</span>
						</p>
					</li>
				</ul>
				<form class="flex-yc">
					<el-input
						type="number"
						v-model="number"
					>
					</el-input>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
import '@/assets/css/computed.css';
import Header from '@/components/Header.vue';
export default {
	components: { Header },
	computed: {
		toVW() {
			return Math.round(((this.number / this.viewport) * 100000).toFixed(4)) / 1000 + 'vw';
		},
		toBaiFenBi() {
			return Math.round(((this.number / this.viewport) * 10000).toFixed(4)) / 100 + '%';
		},
		toPX() {
			return Math.round((this.number * this.viewport) / 100) + 'px';
		},
	},
	data() {
		return {
			viewport: 1920,
			number: 20,
			tipShow: false,
		};
	},
	methods: {
		copyTextToClipboard(text) {
			let that = this;
			this.tipShow = false;
			console.log(text);
			if (!navigator.clipboard) {
				function fallbackCopyTextToClipboard(text) {
					// 1.创建一个可选中元素
					let textArea = document.createElement('textarea');
					textArea.value = text;
					// 2.使用定位，阻止页面滚动
					textArea.style.top = '0';
					textArea.style.left = '0';
					textArea.style.position = 'fixed';
					document.body.appendChild(textArea);
					textArea.focus();
					textArea.select();
					try {
						var successful = document.execCommand('copy');
						var msg = successful ? 'successful' : 'unsuccessful';
						console.log('Fallback: Copying text command was ' + msg);
						that.tipShow = true;
						var iii = setTimeout(() => {
							that.tipShow = false;
							clearTimeout(iii);
						}, 1000);
					} catch (err) {
						console.error('Fallback: Oops, unable to copy', err);
					}
					// 3.移除元素
					document.body.removeChild(textArea);
				}
				fallbackCopyTextToClipboard(text);
				return;
			}
			navigator.clipboard.writeText(text).then(
				function () {
					console.log('Async: Copying to clipboard was successful!');
					that.tipShow = true;
				},
				function (err) {
					console.error('Async: Could not copy text: ', err);
				}
			);
		},
	},
};
</script>
